.header {
	position: relative;
	padding: 18px 30px 27px;
	border-bottom: 1px solid #dcdcdc;
	font-size: 0;
	// display: none
	@include xl {
		padding: 5px 15px 10px; }
	&__inner {
		margin: 0 auto;
		max-width: 1540px;
		@include clr; }
	&__btns {
		float: left;
		padding: 8px 20px 0 78px;
		@include xl {
			padding: 2px 0 0 78px;
			margin-bottom: -2px; }
		@include sm {
			display: none; } }
	&__btn {
		display: inline-block;
		vertical-align: top;
		margin: 5px; }
	&__actions {
		float: right;
		padding: 10px 0 1px;
		margin: 0 -16px 0 29px;
		@include xl {
			display: none; } }
	&__action {
		position: relative;
		display: inline-block;
		vertical-align: top;
		padding: 10px 16px;
		height: 40px;
		color: inherit;
		@include vertical;
		transition: color 0.3s;
		@include hover {
			color: $orange; } }
	&__icon {
		display: inline-block;
		vertical-align: middle;
		font-size: 20px;
		fill: currentColor; }
	&__sticker {
		position: absolute;
		top: -3px;
		right: 0;
		border-radius: 13px;
		min-width: 25px;
		color: $white;
		background: $orange;
		@include f(12px, 400, 25px); }
	&__info {
		float: right;
		padding: 13px 0 0;
		@include xl {
			display: none; } }
	&__phone {
		display: block;
		color: inherit;
		@include f(22px, 700, 19px);
		letter-spacing: 0.01em;
		text-transform: uppercase;
		transition: color 0.3s;
		@include hover {
			color: $blue; } }
	&__schedule {
		margin-top: 9px;
		opacity: 0.5;
		@include f(12px, 400, 1);
		letter-spacing: 0.01em; } }


.logo {
	float: left;
	font-size: 0;
	img {
		@include xl {
			height: 44px; } } }

.nav {
	overflow: visible;
	text-align: center;
	padding: 13px 20px 5px 0;
	@include xl {
		display: none; }
	&__item {
		display: inline-block;
		vertical-align: top; }
	&__link {
		display: block;
		padding: 10px 15px 11px;
		color: currentColor;
		@include f(14px, 400, 1);
		letter-spacing: 0.01em;
		transition: color 0.3s;
		@include hover {
			color: $blue; } } }

.menu {
	position: absolute;
	z-index: 700;
	top: 100%;
	left: 50%;
	overflow: auto;
	width: 100vw;
	// min-width: 1600px
	max-height: calc(100vh - 99px);
	opacity: 0;
	visibility: hidden;
	box-shadow: 0 4px 27px rgba(0, 0, 0, 0.14);
	transform: translate3d(-50%,0,0);
	transition: opacity 0.3s, visibility 0.3s;
	@include xl {
		max-height: calc(100vh - 60px); }
	@include sm {
		position: relative;
		display: none;
		padding: 20px 10px;
		max-height: none;
		opacity: 1;
		visibility: visible;
		box-shadow: none;
		text-align: left; }
	&.is-open {
		opacity: 1;
		visibility: visible; }
	&__container {
		background: $white; }
	&__list {
		clear: both;
		margin: 0 -10px;
		font-size: 0;
		@include supports(column-count, 6) {
			column-count: 6;
			@include md {
				column-count: 4; }
			@include xmd {
				column-count: 3; }
			@include sm {
				column-count: 2; }
			@include xs {
				column-count: 1; } } }
	&__box {
		position: relative;
		display: inline-block;
		vertical-align: top;
		padding: 19px 10px;
		width: (100%/6);
		@include md {
			width: (100%/4); }
		@include xmd {
			width: (100%/3); }
		@include sm {
			width: (100%/2); }
		@include xs {
			width: 100%;
			text-align: center; }
		@include supports(column-count, 6) {
			display: list-item;
			width: auto !important;
			break-inside: avoid-column; } }
	&__title {
		display: block;
		margin-bottom: 12px;
		color: inherit;
		@include f(13px, 700, 18px);
		letter-spacing: 0.01em;
		transition: color 0.3s;
		@include hover {
			color: $blue; } }
	&__link {
		display: block;
		padding: 4px 0;
		color: inherit;
		@include f(13px, 400, 1.25);
		letter-spacing: 0.01em;
		transition: color 0.3s;
		@include hover {
			color: $blue; } } }

.btn-nav {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	display: none;
	padding: 0 15px 0 0;
	@include vertical;
	@include xl {
		display: block; }
	&__title,
	&__icon {
		display: inline-block;
		vertical-align: middle; }
	&__title {
		padding: 1px 0 0 14px;
		color: $dark;
		font-family: $opensans;
		@include f(14px, null, 30px); }
	&__icon {
		position: relative;
		width: 29px;
		height: 11px;
		border-top: 1px solid $dark;
		border-bottom: 1px solid $dark;
		&:before {
			content: '';
			position: absolute;
			top: 4px;
			left: 0;
			right: 0;
			border-top: 1px solid $dark; } } }
