body {
	color: $dark;
	background: $white;
	font-family: $opensans;
	@include f(15px, 400, 1.734); }

.main {
	padding-top: 17px;
	@include md {
		padding-top: 10px; } }

.inner {
	position: relative;
	padding-left: 15px;
	padding-right: 15px;
	margin-left: auto;
	margin-right: auto;
	max-width: 1210px;
	@include clr;
	@include sm {
		padding-left: 10px;
		padding-right: 10px; } }

.section {
	&:not(:last-child) {
		margin-bottom: 20px; } }

.link {
	color: $blue;
	border-bottom: 1px solid transparent;
	transition: border-color .3s;
	@include hover {
		border-color: $blue; } }
