.similar {
	padding-top: 50px;
	overflow: hidden;
	@include xs {
		padding-top: 20px; }
	&__title {
		margin-bottom: 40px;
		letter-spacing: .01em;
		@include clr;
		@include xs {
			margin-bottom: 15px; } }
	&__link {
		float: right;
		margin: 10px 0 0 20px;
		color: $blue;
		@include f(14px, null, 1);
		transition: color .24s;
		@include hover {
			color: $lightBlue; }
		@include xmd {
			display: none; } }
	&__products {
		font-size: 0;
		margin: 0 -30px;
		@include xmd {
			margin: 0 -15px; } }
	&__wrapper {
		display: inline-block;
		vertical-align: top;
		width: 25%;
		padding: 0 30px;
		@include xmd {
			padding: 0 15px;
			width: 50%; } }
	&__item {
		display: block;
		text-align: center;
		padding: 10px 0;
		color: $dark;
		transition: box-shadow .24s;
		@include hover {
			box-shadow: 0 0 18px rgba(0, 0, 1, 0.16); }
		@include xmd {
			margin-bottom: 35px; }
		@include xs {
			padding: 0; } }
	&__img {
		height: 155px;
		margin-bottom: 16px;
		@include vertical;
		@include xs {
			height: 90px; }
		&-in {
			display: inline-block;
			vertical-align: middle;
			max-width: 100%;
			max-height: 100%; } }
	&__label {
		margin-bottom: 5px;
		@include f(14px, null, 1.5);
		letter-spacing: .01em;
		@include xs {
			font-size: 12px; } }
	&__price {
		@include f(14px, 700, 2.143);
		letter-spacing: .01em;
		@include xs {
			font-size: 12px; } } }
