.footer {
	@include f(14px, 400, 19px);
	&__inner {
		padding-top: 55px;
		padding-bottom: 35px;
		@include xmd {
			padding-top: 35px;
			padding-bottom: 25px; }
		@include sm {
			padding-top: 5px;
			padding-bottom: 25px; } }
	&__subtitle {
		display: block;
		color: inherit;
		font-weight: 600;
		@include sm {
			display: none; } }
	&__item {
		padding: 6px 0 5px; }
	&__link {
		color: inherit;
		opacity: 0.7;
		transition: color 0.3s, opacity 0.3s;
		@include hover {
			color: $blue;
			opacity: 1; } }
	&__phone {
		display: block;
		padding: 6px 0 5px;
		color: inherit;
		@include f(18px, 700, 19px);
		text-transform: uppercase;
		transition: color 0.3s;
		@include hover {
			color: $blue; } }
	&__mail {
		display: block;
		color: inherit;
		transition: color 0.3s;
		@include hover {
			color: $blue; }
		strong {
			font-weight: 600; } }
	&__meta {
		padding: 6px 0 5px;
		opacity: 0.7; }
	&__row {
		&:not(:last-child) {
			margin-bottom: 30px; } }
	&__nav {
		float: left;
		width: 34%;
		@include xmd {
			width: 50%; }
		@include sm {
			display: none; } }
	&__col {
		float: left;
		padding-right: 20px;
		width: 50%; }
	&__list {
		padding-top: 9px; }
	&__info {
		float: left;
		padding-right: 20px;
		padding-left: 100px;
		width: 42%;
		@include xmd {
			width: 50%;
			padding: 0; }
		@include sm {
			display: none; } }
	&__media {
		float: right;
		width: 24%;
		max-width: 180px;
		@include xmd {
			width: 100%;
			max-width: 100%;
			padding-top: 35px; }
		@include sm {
			padding-top: 0;
			float: none; }
		&-wrapper {
			@include xmd {
				width: 50%;
				float: right;
				padding-top: 15px; }
			@include sm {
				width: 100%;
				padding-top: 0;
				margin-bottom: 10px;
				text-align: center; } } }
	&__bottom {
		clear: both;
		padding-top: 19px;
		@include clr;
		@include xmd {
			padding-top: 30px; }
		@include sm {
			clear: none;
			&:after {
				display: none; } } }
	&__market {
		width: 100%;
		@include xmd {
			max-width: 300px;
			display: inline-block;
			background-color: #efefef; }
		@include sm {
			width: 50%;
			max-width: 100%;
			float: left; }
		img {
			display: block;
			max-width: 100%;
			@include xmd {
				max-height: 100px;
				margin: 0 auto;
				max-width: 100%; }
			@include sm {
				max-height: 50px; } } }
	&__copy {
		float: left;
		padding-top: 6px;
		padding-right: 20px;
		width: 17%;
		opacity: 0.5;
		@include xmd {
			width: auto;
			padding-right: 30px;
			font-size: 13px; }
		@include sm {
			float: none;
			display: block;
			padding-right: 0;
			text-align: center;
			clear: both;
			padding-top: 20px; } }
	&__rules {
		float: left;
		padding-top: 6px;
		padding-right: 20px;
		width: 25%;
		@include xmd {
			width: auto;
			max-width: 160px;
			font-size: 13px; }
		@include sm {
			clear: both;
			max-width: 100%;
			width: 100%;
			text-align: center;
			padding-right: 0;
			padding-top: 17px; }
		&-link {
			color: inherit;
			opacity: 0.5;
			transition: color 0.3s, opacity 0.3s;
			@include hover {
				color: $blue;
				opacity: 1; } } }
	&__payments {
		float: right;
		width: 58%;
		font-size: 0;
		@include xmd {
			width: 50%;
			float: right; }
		@include sm {
			float: left;
			background: #f6f6f6;
			text-align: center;
			padding: 15px 0; } }
	&__payment,
	&__payment-link {
		display: inline-block;
		vertical-align: middle; }
	&__payment {
		padding-right: 28px;
		@include sm {
			padding: 0 6px;
			margin-bottom: 1px; }
		img {
			display: block;
			@include sm {
				height: 11px; } } } }

a.footer__subtitle {
	transition: color 0.3s;
	@include hover {
		color: $blue; } }

.social {
	margin: 10px -14px 23px;
	font-size: 0;
	@include xmd {
		margin-bottom: 0; }
	@include sm {
		margin-top: 0; }
	&__item,
	&__link {
		display: inline-block;
		vertical-align: top; }
	&__link {
		padding: 14px;
		height: 55px;
		color: inherit;
		opacity: 0.5;
		@include vertical;
		transition: color 0.3s, opacity 0.3s;
		@include hover {
			color: $blue;
			opacity: 1; } }
	&__icon {
		display: inline-block;
		vertical-align: middle;
		fill: currentColor; }
	&__instagram {
		font-size: 27px; }
	&__vk {
		font-size: 16px; }
	&__facebook {
		font-size: 26px; } }

.go-top {
	position: absolute;
	top: 0;
	left: 100%;
	border-radius: 50%;
	width: 67px;
	height: 67px;
	color: $white;
	background: $blue;
	@include vertical;
	transform: translate3d(63px, -50%, 0);
	transition: color 0.3s, background 0.3s;
	@include r(1450) {
		left: auto;
		right: 73px; }
	@include xmd {
		display: none; }
	@include hover {
		background: $lightBlue; }
	&__icon {
		display: inline-block;
		vertical-align: middle;
		fill: currentColor;
		font-size: 9px;
		transform: translateY(-2px); } }
