.contacts {
	&__map {
		position: relative;
		overflow: hidden; }
	&__position {
		position: absolute;
		left: 0;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
		@include sm {
			transform: translate3d(0,0,0);
			top: 0;
			position: static; } }
	&__content {
		padding: 40px 0;
		border-bottom: 1px solid $borderGray;
		@include xmd {
			padding: 25px 0 35px; }
		@include sm {
			padding-top: 0; } }
	&__text {
		&_left {
			p {
				@include xmd {
					margin-bottom: 12px; } } }
		p {
			letter-spacing: .025em;
			@include f(13px, null, 1.847); } } }
.map {
	height: 600px;
	margin: 0 -50%  0 0;
	@include sm {
		height: 280px;
		margin: 0; }
	a[target="_blank"] {
		background-color: inherit;
		display: none; }
	.gm-style div a img {
		display: none; }
	.gm-style-cc {
		display: none; } }

.gmnoprint {
	width: 45px;
	height: 72px; }
