.characteristics {
	&__list {
		&_block {
			.characteristics {
				&__label {
					width: 100%;
					display: block;
					&-in {
						font-size: 14px;
						line-height: 1;
						&:before {
							display: none; } } }
				&__value {
					width: 100%;
					line-height: 1;
					font-size: 14px;
					padding-left: 0; }
				&__item {
					margin-bottom: 20px; } } } }
	&__control {
		color: $blue;
		letter-spacing: .01em;
		@include f(14px, null, null);
		@include sm {
			font-size: 12px; }
		&:hover {
			text-decoration: underline; } }
	&__item {
		margin-bottom: 2px;
		font-size: 0; }
	&__label {
		display: inline-block;
		vertical-align: top;
		width: 38%;
		overflow: hidden;
		line-height: 1;
		@include sm {
			width: 50%; }
		&-in {
			position: relative;
			display: inline;
			color: $gray-lighten;
			letter-spacing: .01em;
			@include f(14px, null, 24px);
			@include sm {
				font-size: 12px; }
			&:before {
				content: "";
				position: absolute;
				width: 2000px;
				left: 100%;
				bottom: 3px;
				border-bottom: 1px dotted #dcdcdc; } } }
	&__value {
		display: inline-block;
		vertical-align: top;
		width: 62%;
		padding-left: 5px;
		color: #202020;
		letter-spacing: .01em;
		@include f(14px, null, 24px);
		@include sm {
			width: 50%;
			font-size: 12px; } } }
