.main-brands {
	padding-top: 30px;
	@include xmd {
		padding-top: 20px; }
	@include xs {
		padding-top: 0; }
	&__items {
		margin: -10px;
		font-size: 0;
		@include xs {
			margin: -5px; } }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 10px;
		width: (100%/6);
		@include xmd {
			width: (100%/4); }
		@include xs {
			width: (100%/3);
			padding: 5px; } }
	&__box {
		display: block;
		text-align: center;
		@include hover {
			.main-brands {
				&__figure {
					background: $white; }
				&__title {
					color: $blue; } } } }
	&__figure {
		border: 1px solid lightgrey;
		height: 115px;
		background: #efefef;
		@include vertical;
		transition: background 0.3s;
		@include xs {
			height: 60px; } }
	&__img {
		display: inline-block;
		vertical-align: middle;
		mix-blend-mode: multiply;
		@include xs {
			max-height: 70%;
			max-width: 70%; } }
	&__title {
		padding-top: 10px;
		padding-bottom: 35px;
		color: $dark;
		@include f(14px, null, 30px);
		letter-spacing: 0.01em;
		transition: color 0.3s;
		@include xmd {
			padding-bottom: 20px; }
		@include xs {
			font-size: 13px;
			padding: 2px 0 5px; } } }
